import tuiActionsheet from '@/components/tui-actionsheet/tui-actionsheet.vue'
import tuiDatetime from '@/components/tui-datetime/tui-datetime.vue'
export default {
	components: {
		tuiActionsheet,
		tuiDatetime
	},
	data() {
		return {
			userInfo: {},
			value: [],
			showActionSheet: false,
			tips: "选择您的性别",
			itemList: [{
				id: 1,
				text: "男",
				color: "#2B2B2B"
			}, {
				id: 2,
				text: "女",
				color: "#2B2B2B"
			}, {
				id: 0,
				text: "不公开",
				color: "#2B2B2B"
			}],
			uploadImg: "", // 上传的头像地址	
			type: 2,
			startYear: 1980,
			endYear: 2020,
			cancelColor: '#888',
			color: '#5677fc',
			setDateTime: '',
			unitTop: false,

		}
	},
	computed: {
		gender() {
			let a = this.userInfo.gender;
			switch (a) {
				case 0:
					return "不公开";
				case 1:
					return "男";
				case 2:
					return "女"
			}
		}
	},
	onLoad(options) {
		this.endYear = new Date().getFullYear()
		this.userInfo = this.$db.get("userinfo") || "";
		if (options.src) this.userInfo.avatar = options.src;
		if (options.tem) this.uploadImg = options.tem;
	},
	watch: {
		userInfo: {
			handler(newVal, oldVal) {
				this.userInfo = newVal;
			},
			deep: true
		}
	},
	methods: {
		showTimePicker() {
			this.$refs.dateTime.show();
		},
		change(e) {
			const age = this.endYear - e.year;
			this.$set(this.userInfo, `age`, age)
		},
		addressPickerChange(e) {
			this.$set(this.userInfo, `province`, e.detail.value[0]);
			this.$set(this.userInfo, `city`, e.detail.value[1]);
		},
		chooseImage() {
			this.$http.uploadImage(1, (res, tem) => {
				if (res.code == 1) {
					uni.navigateTo({
						url: `./cropper?src=${tem}`
					})
				} else {
					this.$common.errorToShow(res.msg);
				}
			})
		},
		itemClick(e) {
			const index = e.index;
			this.$set(this.userInfo, `gender`, this.itemList[index].id);
			this.closeActionSheet();
		},
		openActionSheet() {
			this.showActionSheet = true
		},
		closeActionSheet() {
			this.showActionSheet = false
		},
		confirmSave() {
			uni.showLoading({
				title: "保存中...",
				mask: true
			})
			let {
				nickname,
				avatar,
				province,
				city,
				gender,
				age
			} = this.userInfo;
			
			let data = {
				nickname,
				avatar,
				province,
				city,
				gender,
				age
			}
			if(this.uploadImg) data.avatar = this.uploadImg;
			this.$http.postProfile(data, res => {
				uni.hideLoading()
				if (res.code == 1) {
					uni.showToast({
						icon: "success",
						title: "保存成功",
						mask: true
					})
					setTimeout(() => {
						uni.navigateTo({
							url: "/pages/home/home?page=user"
						})
						uni.hideToast()
					}, 500)

				} else {
					this.$common.errorToShow(res.msg)
				}
			})
		},
	}
}
